
.main-nav{
    
    
    background: #dad7cd;
    padding: 20px 0px;
    position: fixed;
    z-index: 1;
    height: 100px;
    
}

.navbar{
   max-width: 1200px;
   padding: 10px 10px;
}
.navbar a{
    text-decoration: none;
}
.logo{
    color: #2f3e46;
    font-size: 25px;
    font-family:fantasy ;
    text-decoration: none;
}
.navigation a{
    padding-right: 10px;
    color: #2f3e46;
    text-decoration: none;
}
.navigation a:hover{
    color: #2f3e46;
}
.navbar a:hover{
    color: #2f3e46;
}



@media (max-width:435px){
    .navbar{
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;        
    }
}