
.trip{
    background-color: #edede9;
}
.main-trip {
    max-width: 1200px;
    padding-top: 100px;
    margin: auto;
    
}
.trip-title{
    display: flex;
    justify-content: center;
    padding: 20px 0px;
}
.trip-title h1{
    color: #2f3e46;
}
.trip-items{
    padding: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.trip-description{
    display: flex;
    flex-direction: column;
    text-align: center;
}
.trip-list{
    list-style: none;
    background-color: #fff;
    height: min-content;
    padding: 20px;
    line-height: 3.5em;
    box-shadow: 0 5px 25px rgba(1 1 1 /15%);
}
.trip-image{
    padding: 20px;
    max-width: 400px;
    box-shadow: 0 5px 25px rgba(1 1 1 /15%);
}
.trip-btn{
    color: #edede9;
    background-color: #2172e5;
    width: fit-content;
    padding: 15px 25px;
    border: none;
    border-radius: 5px;
    margin: auto;
    
}

@media (max-width:994px){
    .trip{
        height: 100%;
        padding-bottom: 0px;
    }
    .trip-items{
        display: flex;
        flex-direction: column;
        
        
    }
    .trip-description{
        
        display: flex;
        align-items: center;
    }
    .trip-image{
        max-width: 420px;
    }
    .trip-list{
        
        margin: 6em 0em;
    }
    .trip-btn{
        margin-bottom: 2rem ;
    }
}